.sideBar{display:flex;flex-direction:column;background:#1f2339;height:100%;width:241px;font-style:normal;font-weight:400;font-size:14px;line-height:19px}@media screen and (max-width: 1400px){.sideBar{width:180px;overflow:auto}}@media screen and (max-width: 720px){.sideBar{position:fixed;opacity:0;top:41px;left:0;transition:left 1s;border-radius:18px;background:rgba(31,35,57,.9)}}.sideBar_active{opacity:100%}.sideBar_home{display:flex;flex-direction:column;gap:25px;margin-top:31px;margin-left:20px;margin-bottom:85px}.sideBar_home_link{display:flex;gap:10px;align-items:center}.sideBar_home__active_link{color:#43e8ff}.sideBar_items__active_link{color:#23273a}.sideBar_items{display:flex;flex-direction:column;margin-left:16px;margin-right:10px}.sideBar_items_link{display:flex;gap:10px;align-items:center;padding:10px 0 10px 6px;border-radius:10px}.sideBar_items__active_link{color:#23273a;background:linear-gradient(0.25turn, #00edfe, #00fedb)}